export default {
  /*
   * 设置当前病区代码
   * @author jasmine
   * */
  settingCurrentZoom(state, zoom) {
    state.currentZoom = zoom;
  },
  /*
   * 设置当前头部选中数据
   * @author jasmine
   * */
  settingCurrentHeaderData(state, headerData) {
    state.currentHeaderData = headerData
  },
  /*
   * 设置当前卡片标签选中数据
   * @author jasmine
   * */
  settingCurrentCWDMState(state, CWDMState) {
    state.CWDMState.historyState = state.CWDMState.currentState;
    state.CWDMState.currentState = CWDMState;
  }
}



