import axios from 'axios'

let state = {
  /*
   * 当前init数据
   * @author jasmine
   * */
  zoomData: null
};

let getters = {
  /*
   * 当前init数据计算，若无，返回空数组
   * @author jasmine
   * */
  zooms({zoomData}) {
    return zoomData ? zoomData.BQInfos : [];
  }
};

let mutations = {
  settingZooms(state, zooms) {
    state.zoomData = zooms;
  }
};

let actions = {
  fetchZoomData({commit}) {
    axios.get('mock/db/zoomname.json')
      .then(({data}) => {
        commit('settingZooms', data);
      });
  }
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



