let currentZoomKeyName = 'currentZoom';
export default {
  beforeCreate() {
    let cachedCurrentZoom = JSON.parse(window.localStorage.getItem(currentZoomKeyName));
    if (cachedCurrentZoom && typeof cachedCurrentZoom === 'object') {
      this.$store.commit('settingCurrentZoom', cachedCurrentZoom);
    }
  },
  created() {
  },
  computed: {},
  methods: {
    _cacheCurrentZoom(zoom) {
      if (!zoom || typeof zoom !== 'object') {
        throw new TypeError('zoom参数不能为空');
      }
      window.localStorage.setItem(currentZoomKeyName, JSON.stringify(zoom));
    },
  },
}



