import axios from 'axios'
import apiUrl from '../../../static/api/index'

let state = {
  /*
   * 当前listHome数据
   * @author jasmine
   * */
  listHomeData: null,
  /*
   * 当前listHome下的病人信息数据
   * @author jasmine
   * */
  patientInformation: null
};

let getters = {
  /*
   * 当前病区名
   * @author jasmine
   * */
  BQTitle: function (state) {
    return state.listHomeData ? state.listHomeData.BQTitle || '' : '';
  },
  /*
   * 当前卡片数据
   * @author jasmine
   * */
  FZInfos: function (state) {
    return state.listHomeData ? state.listHomeData.FZinfos || [] : [];
  },
  /*
   * 当前头部数据
   * @author jasmine
   * */
  headerData: function (state) {
    return state.listHomeData ? state.listHomeData.BQTotalInfos || [] : [];
  }
};
let mutations = {
  settingData(state, data) {
    state.listHomeData = data;
  },
  settingPatientInformation(state, data) {
    state.patientInformation = data;
  }
};

let actions = {
  fetchListHomeData({rootGetters, commit}) {
    axios.get(apiUrl.homeDataUrl, {
      params: {
        BQDM: rootGetters.currentBQDM
      }
    }).then(({data}) => {
      commit('settingData', data);
    });
  },
  fetchPatientInformation({rootGetters, commit}) {
    axios.get(apiUrl.patientInformationUrl, {
      params: {
        BQDM: rootGetters.currentBQDM,
        CMDW: rootGetters.currentState.CWDM
      }
    }).then(({data}) => {
      commit('settingPatientInformation', data);
    });
  }
};
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



